@import "../../scss/variable.scss";
@import "../../scss/common.scss";
.header{
    @extend .flex;
    flex-direction: column;
    .register{
        @extend .flex-end;
        height: 48px;
        background-color: $blackColor;
        width: 100%;
        padding:0 100px;
    }
    .header-title{
        text-transform: uppercase;
        color: $brownColor;
        @extend .title-bold
    }
    .logo{
    width: 100%;
    height: 90px;
        @extend .flex;
        img{
            width: 130px;
            padding-top: 60px;
            position: absolute;
            object-fit: cover;
        }
    }
}

